.signals-transforms-table {
  border-collapse: collapse;
  margin: auto;
  min-width: 30em;
}
.signals-transforms-table sup a {
  text-decoration: none;
}
.signals-transforms-table sup a:hover {
  text-decoration: underline;
}
.signals-transforms-table [role="button"] {
  cursor: pointer;
}
.signals-transforms-table abbr {
  border-bottom: dotted 1px #444444;
}
.signals-transforms-table caption {
  position: relative;
  border-radius: .5em;
  border: 1px solid #888888;
  height: 1.5em;
  padding: 0;
  font-weight: bold;
  white-space: nowrap;
}
.signals-transforms-table .signal-transform-dropdown {
  display: block;
}
.signals-transforms-table .signal-transform-dropdown::after {
  display: block;
  position: absolute;
  width: 2em;
  height: 1.5em;
  line-height: 1.5em;
  vertical-align: bottom;
  top: 0;
  right: 0;
  content: "▼";
  color: rgba(106, 122, 212, 0.3);
}
.signals-transforms-table .signal-transform-dropdown:hover::after {
  color: #6a7ad4;
}
.signals-transforms-table .signal-transform-select {
  position: absolute;
  z-index: 2;
  border-radius: .5em;
  border: 1px solid #888888;
  background: #FFF;
  color: #000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-weight: normal;
}
.signals-transforms-table .signal-transform-select li {
  border-radius: .5em;
}
.signals-transforms-table .signal-transform-select li:hover {
  background: #EEC;
}
.signals-transforms-table col {
  width: 15%;
}
.signals-transforms-table tr.some-browsers-ignore-col-elements td {
  padding: 0;
  height: 0;
}
.signals-transforms-table colgroup.both col {
  width: 10%;
}
.signals-transforms-table td {
  padding: 1em;
}
.signals-transforms-table td.both {
  vertical-align: top;
}
.signals-transforms-table th {
  height: 1.5em;
  line-height: 1.5em;
  padding: 0;
}
.signals-transforms-table thead {
  border-bottom: solid 1px #888888;
}
.signals-transforms-table tbody {
  border-top: solid 1px #888888;
}
.signals-transforms-table tbody tr:first-child + tr td {
  padding-top: 1.5em;
}
.signals-transforms-table tbody tr:first-child th {
  display: none;
}
.signals-transforms-table tbody tr:last-child td {
  padding-bottom: 1.5em;
}
.signals-transforms-table tbody.is-collapsed tr:first-child th {
  display: table-cell;
  border-bottom: solid 1px #888888;
}
.signals-transforms-table tbody.is-collapsed tr td {
  display: none;
}
.signals-transforms-table tbody:last-child tr:first-child th {
  border-bottom: 0;
}
.signals-transforms-table .cell {
  position: relative;
}
.signals-transforms-table .collapse {
  color: #444;
  position: absolute;
  top: -1.5em;
  width: 100%;
  text-align: center;
  white-space: nowrap;
}
.signals-transforms-table .collapse:hover {
  color: #000;
}
.signals-transforms-table .note {
  display: none;
  position: absolute;
  font-size: 0.8em;
  font-family: sans-serif;
  color: #444444;
  z-index: 1;
}
.signals-transforms-table .arrow {
  position: absolute;
  z-index: -1;
  height: 4px;
}
.signals-transforms-table .arrow .arrowhead {
  display: block;
  position: absolute;
  top: 0;
  height: 4px;
  width: 20px;
}
.signals-transforms-table .arrow .arrowhead.at-tl {
  left: 0;
  transform-origin: left;
  transform: rotate(-45deg);
}
.signals-transforms-table .arrow .arrowhead.at-bl {
  left: 0;
  transform-origin: left;
  transform: rotate(45deg);
}
.signals-transforms-table .arrow .arrowhead.at-tr {
  right: 0;
  transform-origin: right;
  transform: rotate(45deg);
}
.signals-transforms-table .arrow .arrowhead.at-br {
  right: 0;
  transform-origin: right;
  transform: rotate(-45deg);
}
.signals-transforms-table .formula {
  margin: -1em;
  position: relative;
}
.signals-transforms-table .formula .item {
  padding: 1px .5em;
}
.signals-transforms-table .formula .note {
  top: 100%;
  margin-top: -1.25em;
  left: 0;
  text-align: center;
  min-width: 100%;
  background: #eeddbb;
}
.signals-transforms-table .formula .note.at-t {
  top: auto;
  bottom: 100%;
  margin-bottom: -1.25em;
}
.signals-transforms-table .formula .arrow {
  background: #eeddbb;
}
.signals-transforms-table .formula .arrow .arrowhead {
  background: #eeddbb;
}
.signals-transforms-table .formula:hover,
.signals-transforms-table .formula.is-highlighted {
  background: #eeddbb;
}
.signals-transforms-table .formula:hover .note,
.signals-transforms-table .formula.is-highlighted .note {
  display: block;
}
.signals-transforms-table .relation {
  position: absolute;
  width: 2em;
  height: 2em;
  line-height: 2em;
  text-align: center;
}
.signals-transforms-table .relation.at-t,
.signals-transforms-table .relation.at-b {
  left: 50%;
  margin-left: -1em;
}
.signals-transforms-table .relation.at-t {
  top: -1em;
}
.signals-transforms-table .relation.at-b {
  bottom: -1em;
}
.signals-transforms-table .relation.at-l,
.signals-transforms-table .relation.at-r {
  top: 50%;
  margin-top: -1em;
}
.signals-transforms-table .relation.at-l::before,
.signals-transforms-table .relation.at-r::before {
  transform: rotate(-90deg);
}
.signals-transforms-table .relation.at-l {
  left: -2em;
}
.signals-transforms-table .relation.at-r {
  right: -2em;
}
.signals-transforms-table .relation.at-tl::before,
.signals-transforms-table .relation.at-br::before {
  transform: rotate(-45deg);
}
.signals-transforms-table .relation.at-tl {
  top: -1em;
  left: -2em;
}
.signals-transforms-table .relation.at-br {
  bottom: -1em;
  right: -2em;
}
.signals-transforms-table .relation::before {
  position: relative;
  z-index: 1;
  display: block;
  content: "⋮";
  color: #888;
}
.signals-transforms-table .relation .note {
  top: 100%;
  left: 50%;
  transform: translate(-50%, 0);
  text-align: center;
  min-width: 100%;
  background: #ccff88;
  line-height: normal;
}
.signals-transforms-table .relation .note.at-t {
  top: auto;
  bottom: 100%;
}
.signals-transforms-table .relation .note.at-l,
.signals-transforms-table .relation .note.at-r {
  top: 0;
  transform: none;
  min-width: 0;
  min-height: 100%;
}
.signals-transforms-table .relation .note.at-l.one-line,
.signals-transforms-table .relation .note.at-r.one-line {
  line-height: 2.5em;
  white-space: nowrap;
}
.signals-transforms-table .relation .note.at-l {
  left: auto;
  right: 100%;
  padding-left: .5em;
  text-align: right;
}
.signals-transforms-table .relation .note.at-r {
  left: 100%;
  padding-right: .5em;
  text-align: left;
}
.signals-transforms-table .relation.at-t .note.at-l,
.signals-transforms-table .relation.at-b .note.at-l {
  margin-right: -20%;
}
.signals-transforms-table .relation.at-t .note.at-r,
.signals-transforms-table .relation.at-b .note.at-r {
  margin-left: -20%;
}
.signals-transforms-table .relation.at-l .note.at-t,
.signals-transforms-table .relation.at-r .note.at-t {
  margin-bottom: -25%;
}
.signals-transforms-table .relation.at-l .note.at-b,
.signals-transforms-table .relation.at-r .note.at-b {
  margin-top: -25%;
}
.signals-transforms-table .relation .arrow {
  border-top: solid 2px #ccff88;
  border-bottom: solid 2px #ccff88;
}
.signals-transforms-table .relation .arrow .arrowhead {
  top: 1px;
  height: 2px;
  background: #ccff88;
}
.signals-transforms-table .relation .arrow .arrowhead.at-tl,
.signals-transforms-table .relation .arrow .arrowhead.at-bl {
  left: -3px;
}
.signals-transforms-table .relation .arrow .arrowhead.at-tr,
.signals-transforms-table .relation .arrow .arrowhead.at-br {
  right: -3px;
}
.signals-transforms-table .relation:hover,
.signals-transforms-table .relation.is-highlighted {
  background: #ccff88;
}
.signals-transforms-table .relation:hover .note,
.signals-transforms-table .relation.is-highlighted .note {
  display: block;
}
.signals-transforms-table .relation:hover::before,
.signals-transforms-table .relation.is-highlighted::before {
  content: "║";
  color: #000;
}
@media print {
  .signals-transforms-table sup,
  .signals-transforms-table .signal-transform-dropdown::after,
  .signals-transforms-table .signal-transform-select,
  .signals-transforms-table .collapse,
  .signals-transforms-table .relation::before,
  .signals-transforms-table tbody.is-collapsed {
    display: none;
  }
}
